.about{
    display: flex;
    flex-direction: row;
    padding: 50px;
    color: white;
    grid-template-columns:  50% 50%;
    background: #0c0c0c;
    background: linear-gradient(215deg, hsla(0, 3%, 34%, 0.178) 0%,#000000 100%);
    z-index: 1;
}
.about-header{
    text-align: center;
    font-size: 60px;
    color: white;
    text-shadow: aqua 1px 0 5px;
    /* border-bottom: 2px solid aquamarine; */
}

#about-left{
    display: flex;
    justify-content: space-around;
    order: 0;
    flex-wrap: wrap;
    text-align: center;
}
#about-left img{
    width: 550px;
    height: 550px;
    color: #252934;
    overflow: hidden;
    border-radius: 50%;
    border: 1px solid black;
    box-shadow: 0 20px 10px -10px #25293450;
    transition: 300ms;
    transition: all 300ms;
}

#about-right{
    display: flex;
    order: 1;
    flex-wrap: wrap;
    text-align: left;
    padding:5% 5%;
}
#about-right p{
    font-size: 22px;
    font-family: 'Baloo Thambi 2', cursive;
}

@media screen and (max-width: 1100px){
    .about{
        flex-direction: column;
    }
    .about-header{
        font-size: 60px;
    }
    img{
        height: 550px;
        width: 550px;
    }
}

@media screen and (max-width: 900px){
    .about{
        flex-direction: column;
    }
    .about-header{
        margin-top: -8px;
        font-size: 40px;
    }
    #about-left img{
        height: 350px;
        width: 350px;
    }
    #about-right p{
        font-size: 16px;
    }
}
*{
    margin: 0;
    padding:0;
    box-sizing: border-box;
}

/****************
MAIN NAVBAR
*****************/
.navbar{
    top:0;
    right:0;
    width: 100%;
    position:  fixed;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    -webkit-backdrop-filter: saturate(180%) blur(5px);
            backdrop-filter: saturate(180%) blur(5px);
    background-color: hsla(0, 0%, 5%, 0.72);
    color: whitesmoke;
    z-index: 99;
}
.resume{
    flex-grow: 1;
    order: 0;
    display: flex;
    cursor: pointer;
}

.resume a:hover {
    color: rgb(0, 255, 255);
    text-shadow: aqua 1px 0 5px;
}

.navbar-menu{
    cursor: pointer;
    flex-grow: 5;
    order: 1;
    flex-wrap: wrap;
    display: flex;
    justify-content: center;
    
}

/***************
NAVBAR LINKS
***************/
.navbar a{
    float: left;
    display: block;
    color: whitesmoke;
    text-align: center;
    padding: 16px 16px;
    margin-top: 7px;
    margin-bottom: 2px;
    text-decoration: none;
    font-size: 20px;
    transition: 0.5s;
}

.navbar-menu a:hover {
    color: rgb(0, 255, 255);
    /* border-bottom: 2px solid aquamarine; */
    text-shadow: aqua 1px 0 5px;
}


/***************
ICONS IN NAVBAR
****************/

.icons-menu{
    flex: 2 1;
    display: flex;
    order: 2;
    justify-content: center;
    float: right;
    
}
.icons-menu a{
    margin-right: 15px;
    margin-top: 12px;
}
.github{
    background: url(/Portfolio/static/media/igithub.50a301d1.svg) no-repeat;
    align-items: center;
}
.linkedin{
    background: url(/Portfolio/static/media/linkedin.beb00edb.svg) no-repeat;
}


/*************
MEDIA SCREEN
**************/
@media screen and (max-width: 850px){
    .line{
        width: 30px;
        height: 3px;
        background: cornsilk;
        margin: 5px;
    }
    .none{
        display: none;
    }
    .navbar{
        left: 0;
        position: fixed;
        height: 100vh;
        width: 35%;
        flex-direction: column;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        transition: -webkit-transform 0.5s ease-in;
        transition: transform 0.5s ease-in;
        transition: transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
    }
    .navbar-menu{
        
        flex-direction: column;
        justify-content: flex-start;
        flex: 8 1;
    }

    .navbar-menu a{
        opacity: 0;
    }

    .resume{
        margin-top: 40px;
        justify-content: center;
    }
    .hamburger{
        top: 0;
        left: 0;
        position: fixed;
        cursor: pointer;
        padding: 10px;
        z-index: 100;
        border-radius: 8px;
    }

    .hamburger div{
        transition: all 0.5s ease;
    }

    .icons-menu{
        flex-wrap: wrap;
        flex:3 1;
        justify-content: center;
        padding: 10px;
    }
    .icons-menu a{
        margin: 15px;
    }
}

.nav-active{
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

@-webkit-keyframes navLinkFade{
    from{
        opacity: 0;
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }
    to{
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@keyframes navLinkFade{
    from{
        opacity: 0;
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }
    to{
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

.toggle .top{
    -webkit-transform: rotate(-45deg) translate(-5px, 6px);
            transform: rotate(-45deg) translate(-5px, 6px);
}

.toggle .middle{
    opacity: 0;
}
.toggle .bottom{
    -webkit-transform: rotate(45deg) translate(-5px, -6px);
            transform: rotate(45deg) translate(-5px, -6px);
}
