.title{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
    height: auto;
    width: 100%;
    margin-top: 50px;

}

.title h1 > span{
    color: aqua;
    /* text-shadow: 1px 1px 1px red, 0 0 .1em snow, 0 0 0.2em blue; */
}

.title > h1{
    display: flex;
    align-self: center;
    flex-wrap: wrap;
    order: 0;
    color: snow;
    font-size: 7vw;
    /* text-shadow: 1px 1px 1px red, 0 0 .1em aqua, 0 0 0.1em snow; */
    /* text-shadow: 1px 1px 2px red, 0 0 1em blue, 0 0 0.2em blue; */
    /* text-shadow: aqua 1px 0 5px; */
}


.title > h2{
    display: flex;
    order: 1;  
    align-self: center;
    color: snow;
    margin-top: -2%;
    font-size: 24px;
}

@media screen and (max-width: 1300px){
    .title{
        margin-top:18px;
    }
    .h1{
        font-size: 90px;
    }
    .title > h2{
        font-size: 24px;
    }
}
@media screen and (max-width: 850px){
    .title{
        margin-top:18px;
    }
    .h1{
        font-size: calc(50px + 40px*((100vw-850px) / 450));
    }
    .title > h2{
        font-size: 14px;
    }
}