
.contact{
    height: auto;
    width: 80%;
    margin-left: 10%;
    text-align: center;
    padding: 30px;
    /* border: 1px solid aqua;  */
    border-radius: 20px;
    color: white;
    
}

h1{
    margin-top: 10%;
}

label{
    position: relative;
}

.user-label::before{
    content: "";
    position: absolute;
    left: 5px;
    top: -9px;
    bottom: 0;
    padding: 4px;
    width: 20px;
    background: url(../images/user.svg) no-repeat center;
}

.email{
    content: "";
    position: absolute;
    z-index: 2;
    /* left: 2px; */
    padding: 30px;
    padding-left: 10px;
    /* bottom: 0; */
    width: 40px;
    background: url(../images/mail.svg) no-repeat center;
}

.textbox{ 
    font-family: 'Roboto', sans-serif;
    color: black;
    background-color: #ffffff;
  font-size: 18px;
  width: 100%;
  padding: 10px 40px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid aqua;
  border-radius: 4px;
  box-sizing: border-box;
}

.textarea{
    font-family: 'Roboto', sans-serif;
    background-color: #ffffff;
    font-size: 18px;
    width: 100%;
    height: 200px;
    padding: 12px 20px;
    box-sizing: border-box;
    border: 1px solid aqua;
    border-radius: 4px;
    margin: 8px 0;
    resize: vertical;
    min-height: 150px;
}
.submit-button{
    font-family: 'Roboto', sans-serif;
    background-color: #0c0c0c;
    width: 100%;
    font-size: 18px;
    border: 1px solid aqua;
    border-radius: 4px;
    color: white;
    margin: 8px 0;
    padding: 14px 28px;
    text-decoration: none;
    cursor: pointer;
    transition: 0.5s;
}

.submit-button:hover{
    background-color: aqua;
    color: black;
    box-shadow: 1px -1px 20px 4px rgba(70, 212, 255, 0.137);
}

