.skills{
    min-height: 550px;
    height: auto;
    width: auto;
    background-color: #0c0c0c;
    margin: 40px;
  
    display: flex;
    flex-direction: column;
    background: #0c0c0c;
    background: linear-gradient(215deg, #161616 0%,#0f0f0fd5 100%);
    z-index: 1;
}

.skills-col{
    display: grid;
    grid-template-columns: repeat(11, 1fr);
    grid-gap: 3em;
}

h1{
    color: white;
    text-align: center;
    padding-top: 50px;
    padding-bottom: 5%;
    font-size: 50px;
}

.skillbarline{
    border-right: 2px solid snow;
    
}

.skillbarline ul li{
    list-style-type: none;
    color: snow;
    float: right;
    padding: 10px;
    margin-bottom: 55px;
    font-size: 20px;
}
.skillbar{
    border-radius: 3px;
    transition: 0.1s ;
    transition-property: width, background-color;
    justify-content: end;
}

/* Bar colors */

.skillbar.java{
    background-color: 	#FD0E35;
}

.skillbar.aws{
    background-color: 	#29AB87;
}

.skillbar.css{
    background-color: steelblue;
}

.skillbar.react{
    background-color: #FF681F;
}

.skillbar.python{
    background-color: 	#FFFF31;
}

.skillbar.nodejs{
    background-color: 	#A50B5E;
}

.skillbar.c{
    background-color: #00CCCC;
}

.skillbar.c2{
 background-color: 	#5946B2;
}

.skillbar.html5{
    background-color: 	#FF5470;
}

.skillbar-bar{
    height: 100%;
    width: 100%;
    background-color: #0c0c0c;
}

.skills-row{
    color: snow;
    margin-left: 10%;
    margin-top: 1%;
}

.skills-row > ul  li {
    list-style-type: none;
    float: left;
    margin-right: 5%;
    font-size: 20px;
}

@media screen and (max-width: 1000px){
    .skills-col{
        grid-gap: 2em;
    }
}
@media screen and (max-width: 700px){
    .skills-col{
        grid-gap: 1em;
    }
}