*{
    margin: 0;
    padding:0;
    box-sizing: border-box;
}

/****************
MAIN NAVBAR
*****************/
.navbar{
    top:0;
    right:0;
    width: 100%;
    position:  fixed;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    backdrop-filter: saturate(180%) blur(5px);
    background-color: hsla(0, 0%, 5%, 0.72);
    color: whitesmoke;
    z-index: 99;
}
.resume{
    flex-grow: 1;
    order: 0;
    display: flex;
    cursor: pointer;
}

.resume a:hover {
    color: rgb(0, 255, 255);
    text-shadow: aqua 1px 0 5px;
}

.navbar-menu{
    cursor: pointer;
    flex-grow: 5;
    order: 1;
    flex-wrap: wrap;
    display: flex;
    justify-content: center;
    
}

/***************
NAVBAR LINKS
***************/
.navbar a{
    float: left;
    display: block;
    color: whitesmoke;
    text-align: center;
    padding: 16px 16px;
    margin-top: 7px;
    margin-bottom: 2px;
    text-decoration: none;
    font-size: 20px;
    transition: 0.5s;
}

.navbar-menu a:hover {
    color: rgb(0, 255, 255);
    /* border-bottom: 2px solid aquamarine; */
    text-shadow: aqua 1px 0 5px;
}


/***************
ICONS IN NAVBAR
****************/

.icons-menu{
    flex: 2;
    display: flex;
    order: 2;
    justify-content: center;
    float: right;
    
}
.icons-menu a{
    margin-right: 15px;
    margin-top: 12px;
}
.github{
    background: url("../images/igithub.svg") no-repeat;
    align-items: center;
}
.linkedin{
    background: url("../images/linkedin.svg") no-repeat;
}


/*************
MEDIA SCREEN
**************/
@media screen and (max-width: 850px){
    .line{
        width: 30px;
        height: 3px;
        background: cornsilk;
        margin: 5px;
    }
    .none{
        display: none;
    }
    .navbar{
        left: 0;
        position: fixed;
        height: 100vh;
        width: 35%;
        flex-direction: column;
        transform: translateX(-100%);
        transition: transform 0.5s ease-in;
    }
    .navbar-menu{
        
        flex-direction: column;
        justify-content: flex-start;
        flex: 8;
    }

    .navbar-menu a{
        opacity: 0;
    }

    .resume{
        margin-top: 40px;
        justify-content: center;
    }
    .hamburger{
        top: 0;
        left: 0;
        position: fixed;
        cursor: pointer;
        padding: 10px;
        z-index: 100;
        border-radius: 8px;
    }

    .hamburger div{
        transition: all 0.5s ease;
    }

    .icons-menu{
        flex-wrap: wrap;
        flex:3;
        justify-content: center;
        padding: 10px;
    }
    .icons-menu a{
        margin: 15px;
    }
}

.nav-active{
    transform: translateX(0%);
}

@keyframes navLinkFade{
    from{
        opacity: 0;
        transform: translateX(50px);
    }
    to{
        opacity: 1;
        transform: translateX(0px);
    }
}

.toggle .top{
    transform: rotate(-45deg) translate(-5px, 6px);
}

.toggle .middle{
    opacity: 0;
}
.toggle .bottom{
    transform: rotate(45deg) translate(-5px, -6px);
}
