.about{
    display: flex;
    flex-direction: row;
    padding: 50px;
    color: white;
    grid-template-columns:  50% 50%;
    background: #0c0c0c;
    background: linear-gradient(215deg, hsla(0, 3%, 34%, 0.178) 0%,#000000 100%);
    z-index: 1;
}
.about-header{
    text-align: center;
    font-size: 60px;
    color: white;
    text-shadow: aqua 1px 0 5px;
    /* border-bottom: 2px solid aquamarine; */
}

#about-left{
    display: flex;
    justify-content: space-around;
    order: 0;
    flex-wrap: wrap;
    text-align: center;
}
#about-left img{
    width: 550px;
    height: 550px;
    color: #252934;
    overflow: hidden;
    border-radius: 50%;
    border: 1px solid black;
    box-shadow: 0 20px 10px -10px #25293450;
    transition: 300ms;
    transition: all 300ms;
}

#about-right{
    display: flex;
    order: 1;
    flex-wrap: wrap;
    text-align: left;
    padding:5% 5%;
}
#about-right p{
    font-size: 22px;
    font-family: 'Baloo Thambi 2', cursive;
}

@media screen and (max-width: 1100px){
    .about{
        flex-direction: column;
    }
    .about-header{
        font-size: 60px;
    }
    img{
        height: 550px;
        width: 550px;
    }
}

@media screen and (max-width: 900px){
    .about{
        flex-direction: column;
    }
    .about-header{
        margin-top: -8px;
        font-size: 40px;
    }
    #about-left img{
        height: 350px;
        width: 350px;
    }
    #about-right p{
        font-size: 16px;
    }
}