.timeline-header{
    color: white;
}

/* circle */

.timeline-circle{
    height: 40px;
    display: grid;
    justify-content: center;
}
.timeline-circle > div{
    background-color: blanchedalmond;
    border-radius: 50%;
    height: 40px;
    width: 40px;
}

.circle-inner-border{
    border: 5px solid #00bcd4;
}

/* main grid */

.timeline{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    height: auto;
    
}

/* left column */

.left-column{
    display: grid;
    grid-template-rows: 1.5fr 2fr 1.5fr;
    margin-left: 90px;
}

/* middle column */

.middle-column{
    background-color: #00ffff;
    display: grid;
    grid-template-columns: repeat(2 ,1fr);
    grid-column-gap: 1.8%;
    margin-left: 10px;
    margin-right: 10px;
}

.middle-left-col{
    display: grid;
    grid-template-rows: repeat(2 ,1fr);
    grid-row-gap: 1.1%;
}


.middle-right-col{
    display: grid;
    grid-template-rows: 1fr 2fr 1fr;
    grid-gap: 1.1%;
}

.middle-right-col > div , .middle-left-col > div{
    background-color: #0c0c0c;
}


/* right column */

.right-column{
    display: grid;
    grid-template-rows: 2fr 0.5fr 2fr;
    margin: 30px 90px 30px 0px;
}


/* container */

.container{
    background-color: #0e0e0e;
    color: rgb(255, 255, 255);
    display: grid;
    /* text-shadow: aquamarine 1px 0 1px; */
    grid-template-rows: repeat(2 , 1fr);
    border-radius: 5px;
    margin-top: 15px;
    margin-bottom: 15px;
    /* box-shadow: 1px -1px 20px 4px rgba(8,8,8,0.92); */
 
    box-shadow: 0px 20px 10px -10px #25293450;
}
.container > div{
    margin:15px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 17px;
}