@import 'https://fonts.googleapis.com/css?family=Overlock:400,400i,700|Oleo+Script';

#projects {
  width: 80%;
  margin-left: 10%;
  margin-top: 5%;
  color: snow;
  min-height: 100vh;
  font-size: 1.4rem;
  background: #0c0c0c;
  background: linear-gradient(215deg, hsla(0, 3%, 34%, 0.178) 0%,#000000 100%);
  z-index: 1;
    
  }

#projects a,
#projects a:visited {
  color: #f300b4;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-decoration: none;
}

#projects a:hover,
#projects a:active {
  color: #252934;
}

/* Container */
#projects .projects-container {
  max-width: 1400px;
  margin: 0 auto;
  width: 100%;
  
}

  
  /* #projects .heading .separator {
    background: #f300b4;
    width: 150px;
    height: 2px;
    margin: 1rem auto;
  } */
  
  #projects .heading .subtitle {
    font-size: 1.4rem;
    text-align: center;
    width: 70%;
    margin: 0 auto;
    text-align: justify;
  }

  /* Project Details */
#projects .project-details .project-tile {
    font-size: 2rem;
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 0;
    color: #f300b4;
    
  }

  /* Icons */
#projects .project-details .icons {
    margin: 0;
    color: #252934;
  }
  
  #projects .project-details .icons i {
    margin-right: .4rem;
    font-weight: normal;
    font-size: 1.4rem;
  }
  
  /* Text */
  #projects .project-details small {
    font-style: italic;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  #projects .project-details p {
    margin: 1rem 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  /* Buttons */
  #projects .project-details .buttons {
    display: flex;
    justify-content: space-between;
  }
  
  #projects .project-details .buttons a {
    width: 49%;
    padding: .5rem;
    border: none;
    border-bottom: 2px solid aqua;
    color: #252934;
    background: #fafafa;
    font-size: 1.2rem;
    text-align: center;
    transition: 0.5s;
  }
  
  #projects .project-details .buttons a:hover {
    background: aqua;
    color: black;
  }
  #projects .project-details .buttons i {
    font-size: .8rem;
    vertical-align: middle;
    margin-left: .5rem;;
  }
  
  @media only screen and (max-width: 1149px) {
    #projects .project {
      grid-template-columns: 1fr 2fr;
    }
  }
  
  @media only screen and (max-width: 949px) {
    #projects .project {
      grid-template-columns: 1fr;
    }
  }
  
  @media only screen and (max-width: 649px) {
    #projects {
      background: #f0f0f0;
    }
    #projects .projects-container {
      padding: 12rem 0 8rem;
    }
    #projects .project {
      padding: 2rem 0;
    }
  }